import { useState } from "react";
import { NavLink, useNavigate } from "react-router-dom";

import { useAuth } from "../../contexts/AuthContext";
import Swal from "sweetalert2";

import AuthInput from "../../components/AuthInput/AuthInput.jsx";
import AuthPageContainer from "../../components/AuthPageContainer/AuthPageContainer.jsx";
import Button from "../../components/Button/Button.jsx";

import styles from "./RegisterPage.module.scss";

export default function RegisterPage() {
  const [account, setAccount] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  const [accountDuplication, setAccountDuplication] = useState(false);
  const [emailDuplication, setEmailDuplication] = useState(false);

  const { register } = useAuth();

  const navigate = useNavigate();

  const handleClick = async () => {
    if (account.length === 0) {
      return;
    }
    if (name.length === 0) {
      return;
    }
    if (email.length === 0) {
      return;
    }
    if (password.length === 0) {
      return;
    }
    if (checkPassword.length === 0) {
      return;
    }

    const success = await register({
      account,
      name,
      email,
      password,
      checkPassword,
    });
    console.log(success);
    if (success.status === "success") {
      Swal.fire({
        position: "top",
        title: "",
        timer: 1000,
        icon: "success",
        showConfirmButton: false,
      });
      navigate("/login");
      return;
    }
    Swal.fire({
      position: "top",
      title: "",
      text: success.response.data.message,
      timer: 1000,
      icon: "error",
      showConfirmButton: false,
    });
    if (success.response.data.message === "Error: email") {
      setEmailDuplication(true);
    } else if (success.response.data.message === "Error: account") {
      setAccountDuplication(true);
    }
    return;
  };

  return (
    <AuthPageContainer title="">
      <AuthInput
        label=""
        value={account}
        placeholder=""
        onChange={(accountInputValue) => {
          setAccount(accountInputValue);
          setAccountDuplication("");
        }}
        notification={
          accountDuplication ? "Error: account" : "!"
        }
        wordsLimit={50}
        onKeyUp={(event) => {
        if (event.key === 'Enter') {
          handleClick();
        }
        }}
      />
      <AuthInput
        label=""
        value={name}
        placeholder=""
        onChange={(nameInputValue) => setName(nameInputValue)}
        notification="!"
        wordsLimit={50}
        onKeyUp={(event) => {
        if (event.key === 'Enter') {
          handleClick();
        }
        }}
      />
      <AuthInput
        label="Email"
        value={email}
        placeholder="Email"
        onChange={(emailInputValue) => {
          setEmail(emailInputValue);
          setEmailDuplication("");
        }}
        notification={emailDuplication ? "Error: email" : "!"}
        wordsLimit={100}
        onKeyUp={(event) => {
        if (event.key === 'Enter') {
          handleClick();
        }
        }}
      />
      <AuthInput
        label=""
        value={password}
        type="password"
        placeholder=""
        onChange={(passwordInputValue) => setPassword(passwordInputValue)}
        notification="!"
        wordsLimit={20}
        onKeyUp={(event) => {
        if (event.key === 'Enter') {
          handleClick();
        }
        }}
      />
      <AuthInput
        label=""
        value={checkPassword}
        type="password"
        placeholder=""
        onChange={(checkPasswordInputValue) =>
          setCheckPassword(checkPasswordInputValue)
        }
        notification="!"
        wordsLimit={20}
        onKeyUp={(event) => {
        if (event.key === 'Enter') {
          handleClick();
        }
        }}
      />
      <Button title="" size="large" isAction onClick={handleClick}></Button>
      <div className={styles.link}>
        <NavLink to={"/login"}>
          <span className={styles.span}></span>
        </NavLink>
      </div>
    </AuthPageContainer>
  );
}
