import styles from "./SettingPage..module.scss";
import { useState, useEffect } from "react";
import NavContainer from "components/NavContainer/NavContainer";
import Header from "components/Header/Header";
import AuthInput from "components/AuthInput/AuthInput";
import Button from "components/Button/Button";

import { useAuth } from "contexts/AuthContext";
import { settingPage } from "api/setting";
import Swal from "sweetalert2";
import { useNavigate } from "react-router";
export default function Setting() {
  const { isAuthenticated, currentMember } = useAuth();
  const token = localStorage.getItem("token");
  const [account, setAccount] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  const navigate = useNavigate();
  const [isPageLoaded, setIsPageLoaded] = useState(false);
  const [accountDuplication, setAccountDuplication] = useState(false);
  const [emailDuplication, setEmailDuplication] = useState(false);

  useEffect(() => {
    if (!isPageLoaded) {
      // 
      if (isAuthenticated && currentMember) {
        setAccount(currentMember.account);
        setName(currentMember.name);
        setEmail(currentMember.email);
        setIsPageLoaded(true); //  flag
        console.log();
      }
    }
  }, [isAuthenticated, currentMember, isPageLoaded]);

  const handleChange = async () => {
    const success = await settingPage(token, currentMember.id, {
      account,
      name,
      email,
      password,
      checkPassword,
    });
    console.log(success);
    if (success.status === "success") {
      setAccount(account);
      setName(name);
      setEmail(email);

      Swal.fire({
        position: "top",
        title: "",
        timer: 1000,
        icon: "success",
        showConfirmButton: false,
      });
      navigate("/setting");
    } else {
      Swal.fire({
        position: "top",
        title: "",
        text: success.error.response.data.message,
        timer: 1000,
        icon: "error",
        showConfirmButton: false,
      });
      console.log(success.error.response.data.message);
      if (success.error.response.data.message === "Error: email") {
        setEmailDuplication(true);
      } else if (
        success.error.response.data.message === "Error: account"
      ) {
        setAccountDuplication(true);
      }
      return;
    }
  };

  return (
    <div className={`${styles.container} container max-auto`}>
      <div className={styles.navContainer}>
        <NavContainer page="user" />
      </div>
      <div className={styles.middleContainer}>
        <div className={styles.header}>
          <Header title="" />
        </div>
        <div className={styles.input}>
          <AuthInput
            label=""
            value={account}
            placeholder=""
            onChange={(accountInputValue) => {
              setAccount(accountInputValue);
              setAccountDuplication("");
            }}
            notification={
              accountDuplication
                ? "Error: account"
                : "!"
            }
            wordsLimit={50}
          />
          <AuthInput
            label=""
            value={name}
            placeholder=""
            onChange={(nameInputValue) => setName(nameInputValue)}
            notification="!"
            wordsLimit={50}
          />
          <AuthInput
            label="Email"
            value={email}
            placeholder="Email"
            onChange={(emailInputValue) => {
              setEmail(emailInputValue);
              setEmailDuplication("");
            }}

            notification={emailDuplication ? "Error: email" : "!"}

            wordsLimit={100}
          />

          <AuthInput
            label=""
            value={password}
            placeholder=""
            type="password"
            onChange={(passwordInputValue) => setPassword(passwordInputValue)}
            notification="!"
            wordsLimit={20}
          />
          <AuthInput
            label=""
            value={checkPassword}
            placeholder=""
            type="password"
            onChange={(checkPasswordInputValue) =>
              setCheckPassword(checkPasswordInputValue)
            }
            notification="!"
            wordsLimit={20}
          />
        </div>
        <div className={styles.button}>
          <Button title="" size="small" isAction onClick={handleChange} />
        </div>
      </div>
    </div>
  );
}
