import { useState, useEffect } from "react";
import { NavLink, useNavigate } from "react-router-dom";

import { useAuth } from "../../contexts/AuthContext.jsx";
import Swal from "sweetalert2";

import AuthInput from "../../components/AuthInput/AuthInput.jsx";
import AuthPageContainer from "../../components/AuthPageContainer/AuthPageContainer.jsx";
import Button from "../../components/Button/Button.jsx";

import styles from "./LoginPage.module.scss";

export default function LoginPage() {
  const [account, setAccount] = useState("");
  const [password, setPassword] = useState("");
  const [accountNotFound, setAccountNotFound] = useState(false);
  const { login, isAuthenticated, adminAuthenticated } = useAuth();
  const navigate = useNavigate();

  const handleClick = async () => {
    if (account.length === 0) {
      return;
    }
    if (password.length === 0) {
      return;
    }

    const success = await login({
      account,
      password,
    });
    if (success) {
      Swal.fire({
        position: "top",
        title: "",
        timer: 1000,
        icon: "success",
        showConfirmButton: false,
      });
      navigate("/main");
      return;
    }
    setAccountNotFound(true);
    Swal.fire({
      position: "top",
      title: "",
      text: "",
      timer: 1000,
      icon: "error",
      showConfirmButton: false,
    });
  };

  useEffect(() => {
    if (isAuthenticated) {
      navigate("/main");
    } else if (adminAuthenticated) {
      navigate("/admin/main");
    }
  }, [navigate, isAuthenticated, adminAuthenticated]);

  return (
    <AuthPageContainer title=" Alphitter">
      <AuthInput
        label=""
        value={account}
        placeholder=""
        onKeyUp={(event) => {
        if (event.key === 'Enter') {
          handleClick();
        }
        }}
        onChange={(accountInputValue) => {
          setAccount(accountInputValue);
          setAccountNotFound("");
        }}
        notification={accountNotFound ? "" : "!"}
        wordsLimit={50}
      />
      <AuthInput
        label=""
        value={password}
        type="password"
        placeholder=""
        onKeyUp={(event) => {
        if (event.key === 'Enter') {
          handleClick();
        }
        }}
        onChange={(passwordInputValue) => setPassword(passwordInputValue)}
        notification="!"
        wordsLimit={20}
      />
      <Button title="" size="large" isAction onClick={handleClick}></Button>
      <div className={styles.link}>
        <NavLink to={"/register"}>
          <span className={styles.span}></span>
        </NavLink>
        <span></span>
        <NavLink to={"/admin/login"}>
          <span className={styles.span}></span>
        </NavLink>
      </div>
    </AuthPageContainer>
  );
}
