import { useState,useEffect } from "react";
import { NavLink, useNavigate } from "react-router-dom";

import { useAuth } from '../../contexts/AuthContext.jsx';
import Swal from "sweetalert2";

import AuthInput from "../../components/AuthInput/AuthInput.jsx";
import AuthPageContainer from "../../components/AuthPageContainer/AuthPageContainer.jsx";
import Button from "../../components/Button/Button.jsx";

import styles from "./AdminLoginPage.module.scss";

export default function LoginPage () {
  const [account, setAccount] = useState("");
  const [password, setPassword] = useState("");
  const [accountNotFound, setAccountNotFound] = useState(false);
  const { adminLogin, adminAuthenticated } = useAuth();
  const navigate = useNavigate();

  const handleClick = async () => {
    if (account.length === 0) {
      return;
    }
    if (password.length === 0) {
      return;
    }

    const success = await adminLogin({
      account,
      password,
    });
    if (success) {
      Swal.fire({
        position: 'top',
        title: '',
        timer: 1000,
        icon: 'success',
        showConfirmButton: false,
      });
      navigate("/admin/main");
      return;
    }
    setAccountNotFound(true);
    Swal.fire({
      position: 'top',
      title: '',
      text:"",
      timer: 1000,
      icon: 'error',
      showConfirmButton: false,
    });
  };

  useEffect(() => {
      if (adminAuthenticated) {
          navigate('/admin/main');
      }
  }, [navigate, adminAuthenticated]);

  return (
    <AuthPageContainer title="">
      <AuthInput label="" value={account} placeholder="" onChange={(accountInputValue) => {
          setAccount(accountInputValue);
          setAccountNotFound("");
        }}
      notification={accountNotFound ? "" : "!"} wordsLimit={50}
      onKeyUp={(event) => {
        if (event.key === 'Enter'){
          handleClick()
        }
      }}
      />
      <AuthInput label="" value={password} type="password" placeholder="" onChange={(passwordInputValue) => setPassword(passwordInputValue)}
      notification="!" wordsLimit={20}
      onKeyUp={(event) => {
        if (event.key === 'Enter'){
          handleClick()
        }
      }}
      />
      <Button title="" size="large" isAction onClick={handleClick}></Button>
      <div className={styles.link}>
        <NavLink to={"/login"}>
          <span className={styles.span}></span>
        </NavLink>
      </div>
    </AuthPageContainer>
  );
}